






















































































































































































































































































































































.contentBox {
  .detailIcon {
    width: 98%;
    display: flex;
    flex-direction: row-reverse;
    img {
      width: 4vw;
      margin-right: 2vw;
    }
  }
  .appHomeContent {
    padding: 2vw;
    box-sizing: border-box;
    border: 0.5vw dashed #92bec5;
    border-radius: 1vw;
    width: calc(100% - 3vw);
    margin: 0.3vw 1.5vw 1vw;
    display: flex;
    flex-wrap: wrap;
    .contentItem {
      width: 100%;
      display: flex;
      margin-bottom: 2.42vw;
      position: relative;
      .itemLabel {
        width: 20%;
        margin-right: 0.9vw;
        padding-left: 1.8vw;
        font-size: 3.2vw;
        font-weight: 600;
        padding-left: 3vw;
        color: #44aaa9;
        position: relative;
        &::before {
          content: " ";
          position: absolute;
          height: 70%;
          width: 2px;
          top: 15%;
          left: 0;
          background-color: #44aaa9;
        }
        // text-align-last: justify;
      }
      .itemValue {
        flex: 1;
        line-height: 4.8vw;
        background: #f5f8fd;
        font-size: 2.66vw;
        font-weight: 400;
        text-align: left;
        color: #868786;
        padding-left: 1vw;
        position: relative;
        .AddDate {
          line-height: 21px;
          &::after {
            display: block;
            content: "";
            border-width: 1vw;
            border-style: solid;
            border-color: #00eab3 transparent transparent transparent;
            position: absolute;
            right: 3%;
            top: calc(50% - 0.75vw);
          }
        }
        input {
          width: 100%;
          border: none;
          background: #f5f8fd;
          padding: 0.5vw 0;
        }
      }
      .w77 {
        max-width: 77vw;
      }
    }
    .itemLast {
      margin-bottom: 1vw;
    }
  }
}
