













































































































































































































































































































































































.activity {
  width: 100vw;
  height: 100vh;
  background-color: #f9f9f9;
  .detailTop {
    width: 100vw;
    height: 9vh;
    background: linear-gradient(116deg, #41bb64 17%, #0a4fa0);
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    padding: 0 3vw;
    .back {
      width: 7vw;
      height: 7vw;
    }
    .My {
      width: 7vw;
      height: 7vw;
    }
    div {
      font-size: 5vw;
      color: white;
    }
  }
  .detailContent {
    width: 94vw;
    height: 85vh;
    overflow-y: scroll;
    background-color: white;
    margin: 5vw auto 0;
    box-sizing: border-box;
    padding: 3vw 1vw;
    border-radius: 3vw;
    &::-webkit-scrollbar {
      display: none;
    }
    .detailTitle {
      font-size: 4.4vw;
      font-weight: 600;
      color: #484848;
      padding-left: 3vw;
      position: relative;
      margin-left: 1vw;
      &::before {
        content: " ";
        position: absolute;
        height: 70%;
        width: 2px;
        top: 15%;
        left: 0;
        background-color: #484848;
      }
    }
    .detailBom {
      margin-top: 5vw;
      .MandatoryBox {
        margin: 3vw 0 0 0;
        font-size: 3.6vw;
        align-content: center;
        .MandatoryBoxItem {
          display: flex;
          input {
            margin: 0.6vw 3vw 0 2vw;
          }
          div {
            color: #cacaca;
            font-size: 3vw;
            span {
              color: #57a5ca;
              border-bottom: 0.5vw #3099ff solid;
            }
          }
        }
        .color8 {
          font-size: 3vw;
          color: #868786;
          list-style: none;
          margin: 2vw 0 0 1.8vw;
        }
      }
      .SubmitBtn {
        text-align: center;
        height: 6vh;
        line-height: 6vh;
        width: 90vw;
        font-size: 4vw;
        font-weight: 400;
        margin: 4vw auto 0;
        background-color: #15cfa4e5;
        border: 1px solid #97979771;
        color: white;
        border-radius: 1vw;
      }
    }
  }
}
